.footer{
    background: white;
    &__inner{
        padding: 132px 363px 122px 347px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo{
        margin-bottom: 23px;
    }

    &__link{
        &-grey{
            color: #CDCDCD;
            margin-bottom: 10px;
        }
    }

    &__column{
        display: flex;
        flex-direction: column;
        &.catalogColumn{
            max-width: 230px;
        }
        &-title{
            color: #959595;
            margin-bottom: 25px;
            &.littleMargin{
                margin-bottom: 13px;
            }
        }

        &-item{
            line-height: 30px;
            &:not(:last-of-type){
                margin-bottom: 10px;
            }
        }

        &-link{
            color: #3F3F3F;
            @include transition;
            &.big{
                font-size: 25px;
            }
            &:hover{
                color: black;
            }
        }

        &-box{
            &:not(:last-of-type){
                margin-bottom: 32px;
            }
        }

        &-social{
            display: flex;
            align-items: center;
        }
    }

    &__social{
        &-link{
            @include colorSocial(32px);
        }
    }
}