// Картика заднего фона, если она сделана через html
@mixin backgroundImage(){
    position: absolute;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}

//Что на поверх чего-то
@mixin onAb(){
    position: absolute;
    width: 100%;
    height: 100%;
}

// Центрирование объектов с абсолютным позиционированием
@mixin abosluteCenter(){
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

// Центрирование по горизонту с position absolute
@mixin horCenterAbsol(){
    left: 50%;
    transform: translateX(-50%);
}

//Центрирование по вертикали с position absolute
@mixin verCenterAbsol(){
    top: 50%;
    transform: translateY(-50%);
}

//Верх лево absolute
@mixin TLa($top: 0, $left: 0){
    top: $top;
    left: $left;
}

//Верх право absolute
@mixin TRa($top: 0, $right: 0){
    top: $top;
    right: $right;
}

//Низ лево absolute
@mixin BLa($bottom: 0, $left: 0){
    bottom: $bottom;
    left: $left;
}

//Низ право absolute
@mixin BRa($bottom: 0, $right: 0){
    bottom: $bottom;
    right: $right;
}

//Располежение левого и правкого элемента слева и справо absolute
@mixin LRea($classLeft, $pxL, $classRight, $pxR){
    #{$classLeft}{
        left: $pxL;
    }

    #{$classRight}{
        right: $pxR;
    }
}

// Центр по флексу
@mixin flexCenter(){
    justify-content: center;
    align-items: center;
}

//Колонки
//Веншний
// $ai - align-items
@mixin columns($ai: center){
    display: flex;
    justify-content: space-between;
    align-items: $ai;
}
//Сама колонка
@mixin column($number: 3){
    $width: 100% / $number;
    width: $width;
    @content;
}

// Навбар с отсутпами
// $class - название класса или айди или блока
// $MarginRight - отсуп спарва, кроме последнего элемента внутри навигации
@mixin nav($class:a, $marginRight: 0) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    #{$class}:not(:last-of-type){
        margin-right: $marginRight;
    }
}

// Модальное окно(Нужно что бы внутренее окно называлось modal__inner)
// $rgba - цвет заднего фона
// $minWidth - минимальная ширина модалки
// $minHight - минимальная высота модалки
// $maxWidth - максимальная ширина модалки
// $maxHight - максимальная высота модалки
// $backModal - задник модалки
// $borRad - скругление модалки
// $padding - внутренний оступ внутри модалки
@mixin modal($rgba: rgba(0, 0, 0, 0.5), $minWidth: 300px, $minHeight: 50px, $maxWidth: none, $maxHeight: none, $backModal: white, $borRad: 10px, $padding: 10px){
    display: flex;
    @include flexCenter;
    position: fixed;
    height: 100%;
    width: 100%;
    @include TLa;
    z-index: 3;
    pointer-events: none;
    transition: .7s ease all;
    transform: translateY(-100vh);
    opacity: 0;
    &__inner{
        min-width: $minWidth;
        min-height: $minHeight;
        max-width: $maxWidth;
        max-height: $maxHeight;
        background: $backModal;
        border-radius: $borRad;
        padding: $padding;
        transition: .3s all;
        pointer-events: none;
    }
    &__background{
        z-index: 2;
        background: $rgba;
        transition: .5s all;
        opacity: 0;
        height: 100%;
        width: 100%;
        position: fixed;
        pointer-events: none;
        @include TLa;
        &.active{
            opacity: 1;
            pointer-events: all;
        }
    }
    &.active{
        transform: translateY(0);
        opacity: 1;
        pointer-events: all;
        .modal__inner{
            pointer-events: all;
        }
    }
}

// Шрифт
// $fontSize - размер шрифта
// $fontFamily - семейство шрифтов(Если "", то ничего устанвиваться не будет)
// $color - цвет шрифта
@mixin font($fontSize: 16px, $fontFamily: '', $color: black){
    @if $fontFamily != ''{
        font-family: $fontFamily;
    }
    font-size: $fontSize;
    color: $color;
}

//Обьявление grid
// $gtc - количество столбцев
// $gtcw - размер одного столбца(изначально одна фракция)
// $gtr - количество строк
// $gtrw - размер одной строки
@mixin grid($gtc: 1, $gtcw: 1fr, $gap: 0, $gtr: 0, $gtrw: 1fr){
    display: grid;
    grid-template-columns: repeat($gtc, $gtcw);
    @if $gtr != 0{
        grid-template-rows: repeat($gtr, $gtrw);
    } @else {
        grid-template-rows: none;
    }
    gap: $gap;
}

// Grid сетка
// $colStart - начало колонки
// $colEnd - конец колонки
// $rowStart - начало строки
// $rowEnd - конец строки
@mixin gridCol($colStart, $colEnd, $rowStart, $rowEnd){
    grid-column-start: $colStart;
    grid-column-end: $colEnd;
    grid-row-start: $rowStart;
    grid-row-end: $rowEnd;
}

//Кнопка
// $isHoverBack - будет ли изменятся задний фон при наведнии
// $isHovrAll - будет ли изенятся и цвет шрифта и фона
// $backgorund - цвет заднего фона
// $backgroundHover - цвет заднего фона при наведении
// $color - цвет шрифта
// $colorHover - цвет при задвении
// $transition - тразиция
// $isUpper - будет ли шрифт с большими буквами
// $borderRadius - закругление
// $border - обводка
@mixin button($isHoverBack: false, $isHoverAll: false,$background: blue, $backgroundHover: black,$color: black, $colorHover: grey, $transition: .3s all, $isUpper: false, $borderRadius: 0, $border: none){
    background: $background;
    color: $color;
    transition: $transition;
    cursor: pointer;
    border: $border;
    border-radius: $borderRadius;
    @if $isUpper{
        text-transform: uppercase;
    }
    &:hover{
        @if $isHoverAll{
            background: $backgroundHover;
            color: $colorHover;
        } @else if $isHoverBack{
            background: $backgroundHover;
        } @else{
            color: $colorHover;
        }
    }
}

//Миксин для создания квадрата/круга
// $px - размер
// $rounded - закругленость
@mixin box($px, $rounded: 0){
    width: $px;
    height: $px;
    border-radius: $rounded;
}

//Миксин для обчного transition
@mixin transition(){
    transition: .3s all;
}

//Центровка maring по вертикальной середине
@mixin mVC(){
    margin: 0 auto;
}

//Центровка margin по горизонтальной середине
@mixin mHC(){
    margin: auto 0;
}

//Центровка margin
@mixin mC(){
    margin: auto;
}
  