.modal{
    @include modal( rgba(0, 0, 0, 0.5), 532px, 495px, none, none, white, 15px, 47px 76px 33px 81px);

    &__inner.center{
        display: flex;
        flex-direction: column;
        @include flexCenter;
    }
    &__title{
        text-align: center;
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 12px;
        &.bigMargin{
            margin-bottom: 46px;
        }
    }

    &__description{
        text-align: center;
        font-size: 14px;
        margin-bottom: 20px;
    }

    &__form{
        width: 330px;
        display: flex;
        flex-direction: column;
        @include mVC;
        margin-bottom: 34px;
        *{
            margin-bottom: 8px;
        }
        &:last-child{
            margin-bottom: 0;
        }
    }

    &__input,
    &__textarea,
    &__button{
        width: 100%;
    }

    &__textarea{
        max-height: 200px;
        height: 92px;
        resize: vertical;
    }

    &__button{
        padding: 17px 45px;
        font-size: 18px;
        font-weight: 700;
    }

    &__subdescription{
        width: 444px;
        @include font(14px, '', black);
        text-align: center;
    }

    a{
        color: #9F9F9F;
        position: relative;
        text-decoration: underline;
    }
}
