.product{
    background: $backgroundC;
    padding: 60px 0 109px 0;
    &__back{
        margin-bottom: 34px;
    }

    &__product{
        display: flex;
    }

    &__img{
        width: 392px;
        height: 352px;
        object-fit: cover;
        border-radius: 12px;
    }

    &__right{
        margin-left: 62px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &__description{
        @include font(18px, '', #656F7C);
        line-height: 25px;
    }

    &__button{
        padding: 17px 42px;
        text-transform: uppercase;
        font-size: 18px;
        align-self: flex-start;
    }
}