.services{
    background: $backgroundC;
    padding-bottom: 135px;
    &__showAll{
        display: flex;
        align-items: center;
        &:hover{
            svg{
                transform: translateX(2px);
            }
        }
        &-text{
            @include font(14px, '', #3F3F3F);
            text-transform: uppercase;
            margin-right: 12px;
        }

        &-arrow{
            svg{
                transition: .3s ease-in all;
            }
        }
    }

    &__titleLine{
        margin-bottom: 40px;
    }

    &__box{
        @include grid(3, 392px, 24px);
        align-content: center;
    }

    &__service{
        padding: 33px 45px 37px 54px;
        background: white;
        border-radius: 12px;
        box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
        @include transition;
        .car{
            height: 64px;
        }
        > svg{
            margin-bottom: 29px;
            *{
                @include transition;
            }
        }
        .service__line svg{
            @include transition;
        }

        &:hover{
            box-shadow: 0px 1px 25px rgba(1, 4, 5, 0.062);
            >svg path{
                fill: rgba(0,0,0,0.5);
            }
            >svg rect{
                stroke: rgba(0,0,0,0.4);
            }
            .service__line svg{
                transform: translateX(10px);
            }
        }
    }
}

.service{
    &__line{
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        
        svg{
            margin-left: 33px;
        }
    }

    &__title{
        font-size: 24px;
        font-weight: 600;
        color: black;
    }

    &__description{
        @include font(14px, '', $grey);
        font-weight: 300;
        line-height: 20px;
    }
}