.wrapper{
    min-height: 100%;
}

.container{
    max-width: 1253px;
    padding: 0 15px;
    margin: 0 auto;
}

*{
    font-family: 'GothamPro';
}

.globalTitle{
    @include font(32px, 'GothamPro', $darkBlue);
    font-weight: 700;
}

body.noScroll{
    overflow: hidden;
}

.titleLine{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

input,
textarea{
    background: #F4F4F4;
    border-radius: 7px;
    padding: 18px 25px;
    border: none;
    outline: none;
    color: #767676;
    &::placeholder{
        color: #767676;
    }
}

.back{
    color: black;
    display: flex;
    align-items: center;
    &__text{
        @include transition;
        margin-left: 15px;
        font-style: italic;
    }

    &:hover{
        .back__text{
            transform: translateX(5px);
        }
    }
}