.price{
    background: $backgroundC;
    padding: 87px 0px 108px;

    &__title{
        margin-bottom: 45px;
    }

    &__content{
        display: flex;
        align-items: flex-start;
    }

    &__list{
        margin-left: 50px;
        &:not(:last-of-type){
            margin-right: 150px;
        }
    }

    &__item{
        @include font(20px, '', #656F7C);
        position: relative;
        &::before{
            content: '';
            @include box(21px, 6px);
            transform: rotate(45deg);
            background: #00B7C3;
            position: absolute;
            top: 3px;
            left: -50px;
        }
        &:not(:last-of-type){
            margin-bottom: 20px;
        }
    }
}