$height: 35%;
/* В целом блок */
.banners{
    background: $backgroundC;
    padding-bottom: 101px;
    &__inner{
        display: flex;
        flex-direction: column;
    }

    &__bottom{
        display: flex;
        justify-content: space-between;
    }

    &__right{
        width: 100%;
        margin-left: 39px;
        display: flex;
        flex-direction: column;
    }
}


/* Отдельный баннер */
.banner{
    padding: 32px 0px 0px 72px;
    background: white;
    box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
    border-radius: 12px;
    position: relative;

    &__line{
        display: flex;
        align-items: center;
        margin-bottom: 33px;
    }

    &__right{
        position: absolute;
        @include TRa(-64px, 90px);
    }

    &__big{
        display: flex;
        flex-direction: column;
        padding-left: 0;
        height: 100%;
        width: 100%;
        padding-top: 57px;
    }

    &__mini{
        padding-top: 20px;
        &.firstBanner{
            min-height: 228px;
            margin-bottom: 33px; 
        }

        &.secondBanner{
            min-height: 307px;
        }
    }

    &__title{
        font-size: 24px;
        font-weight: 500;
        &.big{
            text-transform: uppercase;
            font-size: 28px;
            margin-bottom: 26px;
        }
    }

    &__description{
        color: #656F7C;
        margin-top: 18px;
        margin-bottom: 37px;
        max-width: 506px;
        line-height: 22px;
        &.long{
            max-width: 717px;
        }
    }

    &__top{
        &.flex{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &.big{
            padding-left: 72px;
            height: $height;
            position: relative;
        }
    }

    &__bottom{
        &.big{
            height: 100% - $height;
            position: relative;
        }
    }

    &__img{
        &-wrapper{
            padding: 16px 20px;
            box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
            border-radius: 19px;
            background: #F5F5F5;
            margin-right: 41px;
        }
    }

    &__background{
        position: absolute;
        @include BLa;
        height: 100%;
        width: 100%;
        object-fit: cover;
        border-radius: 0 0 12px 12px;
    }

    &__logo{
        position: absolute;
        @include TLa(26px, 74px);
        z-index: 2;
    }

    &__topEffect{
        height: 40%;
        width: 100%;
        background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
        position: absolute;
        @include TLa;
    }
}