/* Весь блок с вопросами */
.questions{
    padding: 68px 0px 86px;

    &__top{
        position: relative;
    }

    &__bottom{
        margin-top: 47px;
    }

    &__content{
        margin-top: 60px;
    }

    &__arrow{
        padding: 15px 17px;
        border-radius: 50%;
        background: rgba(211, 211, 211, 0.6);
        cursor: pointer;
        position: absolute;
        @include verCenterAbsol;
    }

    @include LRea('&__leftArrow', 0, '&__rightArrow', 0);

    &__buttons{
        width: 90%;
        margin: 0 auto;
    }

    &__button{
        padding: 27px 72px;
        font-size: 22px;
        background: #F5F5F5;
        color: black;
        box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
        border-radius: 39px;
        font-weight: 500;
        @include transition;
        &.select{
            background: rgba(0, 183, 195, 0.83);
            color: white;
            cursor: default;
        }
        &:not(:last-of-type){
            margin-right: 17px;
        }
    }

    &__steps{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__wrapper{
        display: none;
    }
}

/* Действия */
.question{
    &__step{
        width: 32%;
        background: #F5F5F5;
        box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
        border-radius: 12px;
        padding: 45px 67px 0px 42px;
        min-height: 475px;
        position: relative;
        &-title{
            font-size: 19px;
            font-weight: 500;
            color: black;
            margin-bottom: 23px;
        }
        &-description{
            color: #656F7C;
            margin-bottom: 55px;
            line-height: 22px;
        }

        a{
            color: #00B7C3;
        }
    }
    &__subdescription{
        display: flex;
        line-height: 22px;
    }
    &__linkInc{
        margin-right: 13px;
    }

    &__number{
        color: #DBDBDB;
        font-size: 120px;
        position: absolute;
        @include BRa(-22px, 35px);
    }
}