.header{
    background: $backgroundC;
    &__inner{
        padding: 36px 236px 33px 196px;
        display: flex;
        justify-content: space-between;
    }

    &__list{
        margin-right: 60px;
        @include nav('.header__item', 42px);
    }

    &__link{
        color: #3f3f3f;
        text-transform: uppercase;
        font-size: 14px;
        transition: .3s all;
        &:hover{
            color: black;
        }

        &-line{
            display: flex;
            align-items: center;
            cursor: pointer;
            &.reverse{
                svg{
                    transform: rotate(180deg);
                }
                &:hover{
                    svg{
                        animation: none;
                    }
                }
            }
            svg{
                transition: .3s all;
            }
            a{
                margin-right: 10px;
            }
            &:hover{
                svg{
                    animation: lineAnim .35s;
                }
            }
        }
    }

    &__right{
        display: flex;
        align-items: center;
    }

    &__phone{
        color: #3f3f3f;
        margin-right: 58px;
    }

    &__button{
        padding: 17px 35px;
        text-transform: uppercase;
        font-size: 18px;
        margin-right: 54px;
    }

    &__social{
        display: flex;
        &-link{
            @include colorSocial(17px);
        }
    }

    &__menu{
        padding: 70px 0px 183px;
        background: #F9F9F9;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 1;
        display: none;
        overflow-y: scroll;
        &::-webkit-scrollbar{
            width: 4px;
        }
        &::-webkit-scrollbar-thumb{
            background: #00b7c3;
        }
        &::-webkit-scrollbar-track{
            background-color: #f2f2f2;
        }
    }

    &__mobileMenu{
        background: #f9f9f9;
        width: 100vw;
        height: 100vh;
        display: flex;
        align-items: center;
        flex-direction: column;
        &-wrapper{
            position: absolute;
            @include TLa;
            background: #f9f9f9;
            z-index: 2;
            margin-top: 112px;
        }
        &-list{
            margin-top: 50px;
            margin-bottom: 100px;
        }
    
        &-item{
            text-align: center;
            &:not(:last-of-type){
                margin-bottom: 40px;
            }
        }

        &-link{
            @include font(16px, '', #3F3F3F);
            @include transition;
            text-transform: uppercase;
            &:hover{
                color: black;
            }
        }

        &-button{
            padding: 17px 30px;
            text-transform: uppercase;
            font-size: 18px;
        }
    }
}

.menu__title{
    margin-bottom: 35px;
}