.document{
    padding: 68px 0px 95px;
    &__title{
        margin-bottom: 90px;
    }

    &__imgs,
    &__texts{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__imgs{
        align-items: center;
    }

    &__img,
    &__text{
        &-wrapper{
            width: 25%;
            display: flex;
            justify-content: center;
        }
    }

    &__img{
        height: 240px;
        object-fit: scale-down;
    }

    &__text{
        text-align: center;
        margin-top: 20px;
        @include font(20px, '', #0D2938);
        font-weight: 700;
    }
}