/* Блок со всеми отзовами */
.reviews{
    background: $backgroundC;
    padding: 180px 0px 48px;
    &__top{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__arrows{
        display: flex;
    }

    &__arrow{
        cursor: pointer;
        background: rgba(211, 211, 211, 0.6);
        padding: 15px 18px;
        border-radius: 50%;
    }

    &__leftArrow{
        margin-right: 18px;
    }

    &__bottom{
        margin-top: 38px;
    }
}

/* Отдельный блок с отзывом */
.review{
    &__box{
        background: #FFFFFF;
        box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
        border-radius: 12px;
        padding: 30px 28px 12px 29px;
        &:not(:last-of-type){
            margin-right: 17px;
        }
    }

    &__line{
        display: flex;
        align-items: center;
        margin-bottom: 19px;
    }

    &__title{
        font-weight: 500;
        margin-right: 20px;
    }

    &__star{
        margin-right: 2px;
    }

    &__number{
        @include font(14px, '', #474747);
        margin-left: 3px;
    }

    &__description{
        @include font(13px, '', #474747);
        line-height: 17px;
        margin-bottom: 30px;
    }

    &__city{
        @include font(12px, '', #BABABA);
    }
}