/* Сам блок и стрелки */
.sliderMain{
    background: $backgroundC;
    &__inner{
        padding: 150px 225px 75px 169px;
        position: relative;
    }

    &__arrow{
        padding: 16px 18px 15px 18px;
        background: rgba(211, 211, 211, 0.6);
        border-radius: 50%;
        cursor: pointer;
        position: absolute;
        @include verCenterAbsol;
    }

    @include LRea('&__leftArrow', 92px, '&__rightArrow', 124px);

    .slick-dots{
        display: flex;
        @include flexCenter;
        margin-top: 74px;
        li{
            &:not(:last-of-type){
                margin-right: 10px;
            }

            &.slick-active{
                .slider__dots{
                    transform: scale(1.5);
                    background: black;
                }
                + li{
                    .slider__dots{
                        transform: scale(1.2);
                        background: rgba(0, 0, 0, 0.7);
                    }
                }
            }
        }
    }
}

/* Настройки внутри самого слайдера */
.slider{
    &__slide{
        display: flex;
        justify-content: space-between;
        margin: 0 126px;
    }

    &__dots{
        @include box(7px, 50%);
        background: rgba(0, 0, 0, 0.35);
        @include transition;
    }
}

/* Слайд */
.slide{
    &__title{
        color: $darkBlue;
        font-size: 48px;
        margin-bottom: 25px;
        font-weight: 700;
        max-width: 745px;
        line-height: 60px;
    }

    &__left{
        margin-bottom: 23px;
    }

    &__descirption{
        color: #A6A6A6;
        margin-bottom: 69px;
        font-weight: 300;
    }

    &__button{
        padding: 20px 90px;
        font-weight: 700;
        font-size: 18px;
    }

    &__img{
        width: 414px;
        height: 300px;
        object-fit: scale-down;
    }
}