/* ПК */
@media (max-width: 1600px){
    .header{
        &__inner{
            padding: 36px 80px 33px 89px;
        }

        &__menu{
            padding: 20px 0px 183px;
        }
    }

    .slider{
        &__slide{
            margin: 0 20px;
        }
    }

    .ourTeam{
        &__inner{
            padding: 0px 146px 0px 177px;
        }
    }

    .footer{
        &__inner{
            padding: 132px 123px 122px 190px;
        }
    }
}

@media (max-width: 1440px){
    .header{
        &__list{
            .header__item:not(:last-of-type){
                margin-right: 31px;
            }
        }
    }

    .ourTeam{
        &__inner{
            padding: 0px 146px 0px 93px;
        }
    }
}

@media (max-width:1366px){
    .header{
        &__inner{
            padding: 36px 45px 33px 68px;
        }

        &__link{
            font-size: 12px;
        }

        &__phone{
            margin-right: 25px;
        }
    }
}

@media (max-width: 1280px){
    .header{
        &__button{
            padding: 15px 33px;
            font-size: 14px;
        }
    }

    .footer{
        &__inner{
            padding: 89px 91px 78px 83px;
        }
    }

    .slide{
        &__img{
            width: 383px;
            height: 273px;
        }

        &__title{
            font-size: 24px;
            line-height: 25px;
        }
    }

    .sliderMain{
        &__inner{
            padding: 34px 250px 75px 169px;
        }

        &__leftArrow,
        &__rightArrow{
            transform: none;
            top: 151px;
        }
    }
}

/* Мобильная */
@media (min-width: 961px){
    .header{
        &__socialMobile{
            display: none;
        }

        &__mobileMenu-wrapper{
            display: none;
        }

        &__mobileMenu-btn{
            display: none;
        }
    }

    .document{
        &__mobileColumns{
            display: none;
        }
    }
}
@media (max-width: 960px){
    .globalTitle{
        font-size: 24px;
    }
    /* Адаптивность */
    .header{
        &__inner{
            padding: 31px 33px 31px 29px;
            align-items: center;
        }

        &__right{
            display: none !important;
        }

        &__socialMobile{
            display: flex;
            &-link{
                @include colorSocial(17px);
            }
        }

        &__menu{
            &::-webkit-scrollbar{
                width: 0;
            }

            .container{
                padding: 0 45px;
            }
        }

        &__mobileMenu-btn{
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
            &.reverse{
                span{
                    &:first-of-type{
                        transform: translate(5px, 8px) rotate(-45deg);
                    }
                    &:nth-of-type(2n){
                        transform: translate(5px, 0px) rotate(45deg);
                    }
                    &:last-of-type{
                        position: absolute;
                        opacity: 0;
                        bottom: 0;
                    }
                }
            }
            span{
                width: 34px;
                height: 1px;
                background: black;
                @include transition;
                &:nth-of-type(2n){
                    margin: 7px 0;
                }
            }
        }

        &__mobileMenu-wrapper{
            display: none;
        }
    }

    /* Главная страница */
    .sliderMain{

        &__inner{
            padding: 15px 32px 50px 40px;
        }

        &__arrow{
            display: none !important;
        }

        .slick-dots{
            margin-top: 27px;
        }
    }

    .slider{
        &__slide{
            flex-direction: column-reverse;
            align-items: center;
            margin: 0;
            padding: 0px 10px;
        }
    }

    .slide{
        &__right{
            margin-right: 0;
            margin-bottom: 40px;
        }
        &__img{
            width: 100%;
            height: auto;
        }

        &__title{
            margin-bottom: 17px;
            font-size: 24px;
            line-height: 30px;
        }

        &__descirption{
            margin-bottom: 40px;
            line-height: 22px;
            font-size: 16px;
        }

        &__button{
            width: 100%;
            display: flex;
            justify-content: center;
            padding: 20px 0;
        }

        &__left{
            display: flex;
            flex-direction: column;
        }
    }

    .services{
        &__inner{
            padding: 0 25px;
        }
        &__box{
            @include grid(1, 100%, 13px);
            &.block{
                .services__service{
                    &:nth-of-type(4n),
                    &:nth-of-type(5n),
                    &:last-of-type{
                        display: none;
                    }
                }
            }
        }

        &__service{
            padding: 33px 45px 28px;
        }
    }

    .partners{

        img{
            height: 82px;
        }
    }

    .banners{
        &__inner{
            @include grid(1, 100%, 13px, 0);
            grid-template-rows: 397px 700px;
        }

        &__bottom{
            width: 100%;
        }

        &__right{
            display: none;
        }
    }

    .banner{
        $height: 40%;
        padding: 0;
        &__mini{
            display: none;
        }

        &__line{
            margin-bottom: 13px;
        }
        

        &__top{
            &.big{
                padding-left: 0;
                padding: 0px 20px;
                height: $height;
            }
        }

        &__bottom{
            &.big{
                height: 100% - $height;
            }
        }

        &__right{
            @include horCenterAbsol();
            width: 230px;
        }

        &__img{
            height: 100%;
            width: 100%;
        }

        &__big{
            width: 100%;
        }

        &__outImg{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &__title,
        &__description{
            text-align: center;
        }

        &__title{
            margin-top: 100px;
            &.big{
                margin-top: 45px;
            }
        }

        &__description{
            margin-bottom: 0;
            margin-top: 26px;
            max-width: none;
            padding: 0px 30px;
            &.long{
                max-width: none;
            }
        }

        &__logo{
            @include horCenterAbsol;
        }
    }

    .questions{
        padding: 42px 0px 32px;
        &__title{
            font-size: 32px;
        }
        &__buttons{
            display: flex;
            width: 100%;
            flex-direction: column;
            justify-content: center;
        }

        &__button{
            display: flex;
            justify-content: center;
            font-size: 20px;
            padding: 30px;
            &:not(:last-of-type){
                margin-bottom: 13px;
                margin-right: 0;
            }
        }

        &__arrow{
            display: none !important;
        }

        &__wrapper{
            overflow-x: scroll;
            overflow-y: hidden;
            padding-bottom: 33px;
            &::-webkit-scrollbar{
                height: 4px;
            }
            &::-webkit-scrollbar-track{
                background-color: #F2F2F2;
            }
            &::-webkit-scrollbar-thumb{
                background: #00B7C3;
                border-radius: 3.5px 3.5px 0px 0px;
            }
        }
    }

    .question{
        &__step{
            width: 323px;
            padding: 31px 25px 0px 35px;
            &-title{
                font-size: 16px;
            }
            &-description{
                font-size: 14px;
                line-height: 20px;
            }

            &:not(:last-of-type){
                margin-right: 19px;
            }
        }

        &__subdescription{
            font-size: 14px;
            line-height: 20px;
        }
    }

    .reviews{
        padding: 25px 0px 43px;
        &__top{
            justify-content: center;
        }

        &__bottom{
            margin-top: 23px;
        }

        &__arrows{
            display: none;
        }
    }

    .review{
        &__box{
            display: none;
            padding: 30px 44px 12px;
            &:not(:last-of-type){
                margin-right: 0;
                margin-bottom: 15px;
            }
        }

        &__description{
            font-size: 12px;
        }
    }

    .faq{
        margin-bottom: 38px;
        .container{
            background: white;
        }

        &__inner{
            padding: 42px 20px 66px 20px;
        }

        &__title{
            text-align: center;
        }

        &__description{
            font-size: 14px;
            max-width: 306px;
        }
    }

    /* Страница продукта */
    .product{
        padding: 60px 0px 79px 0px;
        &__product{
            flex-direction: column;
        }

        &__img{
            width: 100%;
            height: 270px;
        }

        &__right{
            margin-left: 0;
            margin-top: 34px;
        }

        &__title{
            margin-bottom: 20px;
        }

        &__description{
            margin-bottom: 45px;
            font-size: 14px;
            line-height: 20px;
        }
    }

    .document{
        &__imgs,
        &__texts{
            display: none;
        }

        &__mobileColumns{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        &__mobileColumn{
            display: flex;
            flex-direction: column;
            align-items: center;
            &:not(:last-of-type){
                margin-bottom: 89px;
            }
        }

        &__mobileText{
            @include font(20px, '', #0D2938);
            margin-top: 60px;
            text-align: center;
            font-weight: 700;
        }
    }

    .price{
        &__content{
            flex-direction: column;
        }

        &__title{
            font-size: 32px;
            margin-left: 20px;
        }

        &__list{
            &:not(:last-of-type){
                margin-right: 0;
                margin-bottom: 20px;
            }
        }

        &__item{
            font-size: 16px;
            &::before{
                content: '';
                @include box(16px, 6px);
                @include TLa(2px, -26px);
            }
        }
    }

    /* Статьи */
    .article{
        padding: 34px 0px 122px;
        &__title{
            @include font(20px, '', black);
        }

        &__description{
            line-height: 21px;
        }
    }

    .footer{
        &__inner{
            padding: 100px 0px 85px;
            flex-direction: column;
            align-items: center;
        }

        &__column{
            &:not(:last-of-type){
                margin-bottom: 71px;
            }

            &-title,
            &-item{
                text-align: center;
            }

            &-title{
                &.littleMargin{
                    text-align: left;
                }
            }
        }

        &__logo{
            margin-bottom: 30px;
        }

        &__link{
            &-grey{
                text-align: center;
                margin-bottom: 17px;
            }
        }

        &__copyright{
            text-align: center;
            font-size: 11px;
        }
    }
}

@media (max-width: 720px){
    .modal{
        &__inner{
            min-width: auto;
            min-height: auto;
            max-width: 450px;
        }

        &__form{
            width: auto;
        }

        &__subdescription{
            width: auto;
        }
    }
}

@media (max-width: 510px){
    .modal{
        padding: 0 20px;
        &__inner{
            max-width: none;
            width: 100%;
        }
    }
}

@media (max-width: 423px){
    .header{
        &__logo{
            width: 140px;
        }

        &__mobileMenu{
            &-wrapper{
                margin-top: 99px;
            }
        }
    }

    .partners{
        img{
            height: 61px;
        }
    }
}

@media (max-width: 400px){
    .questions{
        &__button{
            font-size: 16px;
        }
    }

    .modal{
        &__inner{
            padding: 47px 30px 33px 30px;
        }
    }
}