.faq{
    padding-bottom: 105px;
    background: $backgroundC;
    &__inner{
        background: #FFFFFF;
        box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
        border-radius: 12px;
        padding: 52px 69px 60px 60px;
    }

    &__title{
        margin-bottom: 54px;
    }

    &__description{
        font-weight: 300;
        line-height: 22px;
    }

    &__top{
        display: flex;
        justify-content: space-between;
    }

    &__miniTitle{
        @include font(20px, '', #0D2938);
        font-weight: 700;
    }

    &__close{
        @include transition;
        cursor: pointer;
        &.select{
            transform: rotate(45deg);
        }
    }

    &__bottom{
        margin-top: 20px;
        display: none;
    }

    &__line{
        width: 100%;
        border: 0.5px solid #EEEEEE;
        margin-top: 30px;
    }

    &__question{
        &:not(:last-of-type){
            margin-bottom: 32px;
        }
    }
}